div#content{
    margin: 0 auto 50px;
    width: 100%;
    max-width: 1200px;
}

#container{
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width:100%;
}

#container #searchBar input[type="text"]{
    box-sizing: border-box;
    border: 1px solid rgb(178, 178, 178);
    border-radius: 3px;
    width: 300px;
    height: 40px;
}

#boardTop{
    display: flex;
    height: 40px;
    justify-content: space-between;
}

#boardTop #searchBar{
    display: flex;
    height: 40px ;
}

form#keywordSearchBar{
    display: flex;
}

#container #searchBar input[type="text"]:focus-visible{
    outline: 2px solid rgb(187, 205, 110);
}

form#keywordSearchBar button[type="submit"]{
    width: 45px;
    height: 40px;
    right: 45px;
    position: absolute;
    right: 0;
    background: none;
    border: none;
}

form#certSearchBar{
    margin-left: 10px;
}

form#certSearchBar input#searchCert{
    width:200px;
    font-size: 1.1rem;
    padding-left: 10px;
}

#selectCert{
    display: none;
    position: relative;
    top: 2px;
    min-width: 150px;
    border: 1px solid gray;
    border-radius: 5px;
    padding: 3px;
    background: white;
}

#selectCert ul{
    margin: 0;
    padding:0;
   list-style-type: none;
}

#selectCert li{
    font-size: 0.8rem;
    margin: 2px;
}

#boardTop button[type="button"]{
    height: 40px;
    cursor: pointer;
}

#boardTop button.askRegButton{
    font-family: Pretendard-Regular;
    font-size: 1rem;
    color: white;
    background: #8cbf75;
}

/*Pagenation*/

#pagenation{
    position: absolute;
    display: flex;
    right: -45px;
    top:30%;
    flex-direction: column;

}

#pagenation .page{
    color: rgb(50, 50, 50);
    font-size: 0.8rem;
    width: 30px;
    padding: 5px;
    text-align: center;
    border-radius:  5px;
    background-color: rgb(213, 213, 213);
    margin-bottom: 5px;
    border: none;
    cursor: pointer;

}

#pagenation .active{
   border: none;
   color: white;
   background-color: rgb(187, 205, 110);


}

/*AskLIstItem*/

#askListItem{
    display: flex;
    width: 100%;
    height: 90px;
    margin-bottom: 15px;
    box-sizing: border-box;
    background-color: rgb(250, 250, 250);
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    align-items: center;
    padding: 0 10px;
}
#askListItem #profile{
    justify-content: center;
    align-items: center;
    display: flex;
    width: 70px;
    height: 100%;
 
}

#askListItem #profile #profileImg{
    width: 60px;    
}

#askListItem #contentBox{
    color: #0d0d0d;
    padding: 0 10px;
    width: 80%;
    min-width: 0;
}

#askListItem #title{
    display: flex;
    align-items: center;
    height: 40px;
    overflow: hidden;
    width: 100%;
    font-size: 1.1rem;
   
}

#askListItem span{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 1.1;
    width: 100%;
}


#askListItem #writer{
    display: flex;
    height: 30px;
    font-size: 0.9rem;
    color: gray;
}

#askListItem #regdate{
    display: flex;
    height: 40px;
    align-items: center;
    font-size: 0.9rem;
    color: gray;
    justify-content: flex-end;
    min-width: 130px;
}

#askListItem #count{
    display: flex;
    justify-content: flex-end;
    height: 30px;
    align-items: flex-start;
    font-size: 0.9rem;
    color: rgb(255, 255, 255);
   
}

/*AskReg*/
#RegContainer{
    padding-left: 40px;
    display: flex;
}

#certContainer{
    margin-top: 40px;
    border-radius: 20px;
    max-width: 500px;
    min-height: 500px;
    width: 50%;
    height: fit-content;
    background: white;
    box-shadow: 4px 12px 30px 6px rgb(0 0 0 / 9%);
}

#certContainer #certResultContainer{
    display: flex;
    flex-wrap: wrap;
    padding: 20px 20px 40px 20px;
    align-content: flex-start;
    justify-content: center;

}

#certResultContainer .certItem{
    display: flex;
    align-items: center;
    white-space: nowrap;
    padding: 4px 12px;
    border-radius: 19px;
    height: 30px;
    margin: 0 7px;
    margin-bottom: 10px;
    background:  lightgray;
    font-size: 1.2rem;
    color: #0d0d0d;
    cursor: pointer;
}

#writeContainer{
    margin-top: -60px;
    max-width: 800px;
    min-height: 400px;
    width: 75%;    
}

#writeContent{
    max-width: 800px;
    width:100%;
    background-image: url('../../images/askWriteContent.png');
    background-size: 100%;
}

#regAskForm{
    margin: 0 auto;
    max-width: 600px;
    width: 70%;
}

#RegContainer #searchCertForm{
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    width: 100%;
    justify-content: center;
}


.askbtnBox{
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
}
  
.askInRegButton{
    margin-left: 10px !important;
}

/*askDetail*/
#askDetailContainer{
    position: relative;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

#askDetailContainer #detailContentBox{
    position: sticky;
    top: 0;
    background: white;
    padding: 20px;
    max-width: 610px;
    min-height: 400px;
    width: 50%;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.15);
    height: fit-content;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#askDetailContainer #detailReply{
    max-width: 500px;
    width: 42%;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    background: white;
    margin-left: 20px;
    height: fit-content;
}
#regAskForm .ql-editor{
    min-height: 300px;
}

#askDetailContainer #detailContentBox .detailWrite{
    display: flex;
    align-items: center;
    height: 40px;
    font-size: 1rem;
    color: rgb(100, 100, 100);
}

#askDetailContainer #detailContentBox .detailWrite #profileImg{
    height: 40px;
    margin-right: 10px;
}

#askDetailContainer #detailContentBox .detailTitle{
    margin-top: 20px;
    word-break: break-all;
    font-size: 1.1rem;
}

#askDetailContainer #detailContentBox .detailContent{
    height: 100%;
    margin-top: 15px;
    padding: 0 3px;
    line-height: 1.2;
    color:#0d0d0d;
    word-break: break-all;
    white-space: pre-line;
}

#askDetailContainer #detailContentBox .detailRegDate{
    display: flex;
    justify-content: end;
    margin-top: 15px;
    color: rgb(100, 100, 100);
    font-size: 0.8rem;
}

#askDetailContainer #detailContentBox .detailLink{
    margin-top: 20px;
}


#askDetailContainer #detailContentBox .detailLink a{
    color: rgb(100, 100, 100);
    font-size: 0.9rem;
    margin-right: 5px;
}

/*AskDetailReply*/
#askDetailContainer #detailReply .askReplyBox{
    min-height: 40px;
    padding: 20px;
    display: flex;
    align-items: flex-start;
    max-width: 460px;
    width: 100%;
}
#askDetailContainer #detailReply .askReplyBox #profileImg{
   width: 40px;
}

#askDetailContainer #detailReply #askReplyRegBox #replyRegForm{
    display: flex;
    justify-content: space-between;
    margin-left: 20px;
    width: 100%;
}

#askDetailContainer #detailReply #replyBox{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    width: 100%;
}

#askReplyBox #replyBox .askReplyContent{
    white-space: normal;
    margin-top: 10px;
    color: #0d0d0d;
    padding-bottom: 10px;
    word-break: break-all;
}

.rereplyOpen{
    display: flex;
    align-self: flex-end;
    justify-self: end;
}

.rereplyOpen p{
    display: flex;
    font-size: 0.7rem;
    margin-right: 5px;
}