.certContainer {
  width: 300px;
  min-height: calc(100vh - 130px);
  box-shadow: 5px 6px 22px lightgray;
  margin-top: 40px;
}
.certfindinput {
  width: 200px;
  height: 40px;
  margin-left: 8%;
  margin-top: 60px;
  border: 1px solid lightgray;
  border-radius: 15px;
  font-size: 14px;
  padding: 0 5px;
}
.smallFindCert {
  font-size: 14px;
  margin: 5px;
  height: 40px;
  border-radius: 30px;
  background: #eee;
  line-height: 20px;
  border: none;
  cursor: pointer;
}

.smallFindCert:hover {
  background-color: black;
  color: white;
}

.certFind {
  width: 850px;
  height: 500px;
  color: black;
  margin-top: 40px;
}
.certFindTitle {
  font-size: 40px;
  padding: 10px;
}

.certFindInfo {
  display: flex;
  justify-content: space-between;
}

.infoHead {
  padding: 10px;
}

.infoBody {
  text-align: center;
  font-size: 12px;
  width: 50px;
}

.infoTable {
  width: 100%;
  height: max-content;
  border-collapse: collapse;
  border-radius: 10px;
  border-style: hidden;
  box-shadow: 0 0 0 1px rgba(150, 150, 150, 0.3);
}

.infoTable > thead > tr > th {
  background-color: rgba(150, 150, 150, 0.3);
  border-top: 1px solid rgba(150, 150, 150, 0.3);
  padding: 15px 5px;
  text-align: center;
}

.infoTable > tbody > tr > td {
  margin-left: 100px;
  border-top: 1px solid #bdbdbd;
  padding: 15px 5px;
  text-align: center;
}

.infoButton {
  font-size: 14px;
  height: 40px;
  border-radius: 30px;
  background: #eee;
  line-height: 20px;
  border: none;
  cursor: pointer;
  padding: 10px 30px;
  margin: 40px 330px;
}
.appInfoTitle {
  font-size: 18px;
  list-style: none;
}
.appInfoCon {
  color: black;
}

.testInfo {
  padding: 10px;
}

.certFindSearchBar {
  padding: 0 15px;
}

.certFindName {
  font-size: 14px;
  float: right;
  margin: 20px 5px;
}
