.certtitle {
  text-align: center;
  font-size: 45px;
  color: black;
  margin-top: 40px;
}

.certinput {
  border-radius: 25px;
  padding: 0 15px;
  border: 3px solid lightgray;
  width: 100%;
  height: 60px;
  margin-top: 60px;
  box-sizing: border-box;
  font-size: 20px;
}

.smallcert {
  height: 37px;
  padding: 10px 15px;
  border-radius: 30px;
  background: #eee;
  font-size: 16px;
  line-height: 10px;
  letter-spacing: -0.5px;
  border: none;
  cursor: pointer;
  margin: 10px;
}

.smallcert:hover {
  background-color: black;
  color: white;
}

.certSearchBar {
  width: 650px;
  position: relative;
}

.certMainCard {
  width: 100%;
  display: flex;
  justify-content: center;
}

.certMainBody {
  width: 800px;
  height: 250px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin-top: 20px;
  margin-bottom: 40px;
  border-radius: 14px;
  box-shadow: 4px 12px 30px 6px rgb(0 0 0 / 9%);
  padding: 10px;
}

.certMainBody:hover {
  transition: all 0.3s ease;
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
  -ms-transform: translateY(-5px);
  box-shadow: 4px 12px 20px 6px rgb(0 0 0 / 18%);
}
.certTitleImg {
  width: 60px;
  height: 60px;
}
.certTitle1 {
  display: flex;
  align-items: flex-end;
  margin: 40px;
}
